import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

import { mapState } from "vuex";

export default {
    data() {
        return {
            theme: {
                color: '#ff4544'
            },
            tabList: [
                {id:0, name: '全部'},
                {id:1, name: '待付款'},
                {id:2, name: '已付款'},
                {id:3, name: '已完成'},
            ],
            loading: null,
            list: [],
            activeTab: 0,
            id: null,
            page: 2
        }
    },
    components: {
        "app-tab-nav": appTabNav,
    },
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall,
            custom_setting: state => state.mallConfig.share_setting_custom,
            share_setting: state => state.mallConfig.share_setting,
        })
    },
    methods: {
        open(e) {
            if(this.id != e) {
                this.id = e;
            }else {
                this.id = null;
            }
        },

        tabStatus(e) {
            this.list = [];
            this.page = 2;
            this.activeTab = e.currentTarget.dataset.id;
            uni.showLoading({
                title: '加载中...'
            });
            this.getList();
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.share.share_order,
                data: {
                    status: that.activeTab
                },
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.$hideLoading();
                uni.hideLoading();
                that.$event.on(that.$const.EVENT_USER_LOGIN).then(()=>{
                    that.getList();
                });
            });
        },
        getMore() {
            let that = this;
            let url;
            uni.showLoading({
                title: '加载中...'
            });
            that.$request({
                url: that.$api.share.share_order,
                data: {
                    status: that.activeTab,
                    page: that.page
                },
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    if(response.data.list.length > 0) {
                        that.loading = null;
                        that.list = that.list.concat(response.data.list);
                        that.page++;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(e => {
                uni.hideLoading();
            });
        },
        toGoods(id,item) {
            if(item.mch_id > 0) {
                uni.navigateTo({
                    url: '/plugins/mch/goods/goods?id=' + id +'&mch_id=' + item.mch_id
                });
            }else if(item.sign =='advance') {
                uni.navigateTo({
                    url: '/plugins/advance/detail/detail?id=' + id
                });
            }else if(item.sign =='pintuan') {
                uni.navigateTo({
                    url: '/plugins/pt/goods/goods?goods_id=' + id
                });
            }else if(item.sign =='miaosha') {
                uni.navigateTo({
                    url: '/plugins/miaosha/goods/goods?id=' + id
                });
            } else if(item.sign =='gift') {
                uni.navigateTo({
                    url: '/plugins/gift/goods/goods?id=' + id
                });
            } else if(item.sign =='advance') {
                uni.navigateTo({
                    url: '/plugins/advance/detail/detail?id=' + item.goods_id
                });
            }  else if(item.sign =='booking') {
                uni.navigateTo({
                    url: '/plugins/book/goods/goods?goods_id=' + id
                });
            } else if(item.sign =='') {
                uni.navigateTo({
                    url: '/pages/goods/goods?id=' + id
                });
            } else if(item.sign == 'vip_card') {
                return false
            }else {
                uni.navigateTo({
                    url: '/plugins/'+item.sign+'/goods/goods?goods_id=' + id
                });
            }

        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        uni.setNavigationBarTitle({
            title: that.custom_setting.menus.order.name
        });
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.getList();
    },
    onReachBottom() {
        this.getMore();
    }
}
